html, body{
  height: auto;
  width: 100%;
  margin-right: -4px;
  margin-left: 4px;

}
.chat-container {
  background-color: rgb(238, 239, 252);
  min-height: calc(100vh - 3rem);
  width: calc(100vw - 1rem) !important;
  font-family: "Poppins", sans-serif;
  padding-bottom: 32px;
  position: relative;
}

.header {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: rgb(254, 254, 254);
  position: fixed;

}

.profile-picture {
  margin-top: 10px;
}

.profile-name {
  font-size: 16px;
  margin-bottom: 0px;
}

.comeback-time {
  font-size: 12px;
  color: rgb(81, 81, 81);
  margin-top: 0px;
}


.chat-window {
  overflow:auto;

  max-height: 520px;
  background-color: rgb(238, 239, 252);
}

img {
  width: 24px;
  margin-right: 8px;
}

.chat-time {
  display: flex;
  align-items: center;
  padding-top: 12%;
}

.line {
  flex-grow: 1;
  height: 0.5px;
  background-color: rgb(127, 126, 125);
  margin: 8px;
}

.time-label {
  color: rgb(127, 126, 125);
  font-size: 12px;
}

.question {
  display: flex;
  justify-content: flex-end;
  margin-top: 16px;
  margin-right: 8px;
  margin-left: 8px;

}

.question-content {
  background-color: rgb(59, 130, 253);
  padding: 8px 16px;
  color: rgb(255, 255, 255);
  font-size: 14px;
  border-top-right-radius: 8px;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;
  width: fit-content;

}

#sender {
  margin-right: 8px;
  margin-top: 16px;
  max-width: 680PX;
  margin-left: 8px;
  display: flex;
}

.message-content {
  background-color: rgb(255, 255, 255);
  padding: 8px 16px;
  font-size: 14px;
  border-top-right-radius: 16px;
  border-top-left-radius: 8px;
  border-bottom-right-radius: 16px;
  width: fit-content;


}

.input-area {

  background-color: rgb(255, 255, 255);
  border-radius: 8px;
  margin: 16px 16px;
  display: flex;
  justify-content: space-between;
  padding-left: 16px;
  border: 1px solid #1976d2 !important;
  bottom: 40px;
  left: 16px;
  right: 16px;
  position: fixed;
}



#fullWidth {
  font-size: 14px;
  width: 100%;


}

.MuiInputBase-input {
  white-space: pre-wrap;
  word-wrap: break-word;
}

.css-7obqij-MuiInputBase-root-MuiInput-root:before {
  border-bottom: 0px solid rgba(233, 35, 35, 0.42) !important;
}

.css-7obqij-MuiInputBase-root-MuiInput-root:after {
  border-bottom: 0px solid #ffffff !important;
  border: 1px solid #1976d2 !important;

}




button {
  border: 0px;
  background-color: rgb(255, 255, 255, 0%);
  display: flex;
}

@media only screen and (max-width: 400px) {
  .chat-window {
    height: 440px;

  }

  .chat-time {

    padding-top: 24%;
  }

  .input-area {
    bottom: 24px;
  }


  .question {
    display: flex;
    justify-content: flex-end;
    margin-top: 16px;
    margin-right: 8px;
    margin-left: 8px;

  }
}